import React from "react"

import {
    HashRouter as Router,
    Switch,
    Route
} from "react-router-dom"

import { AuthProvider, PrivateRoute } from "@meddbriefer/mdb-auth"

import "bootstrap/dist/css/bootstrap.min.css";

import "./styles.css";

import { PreferencesProvider } from "./contexts/PreferencesContext"

import Scenario from "./routes/Scenario"
import ScenarioList from "./routes/ScenarioList"

import UnauthedLayout from "./components/UnauthedLayout"

const App = (props) => {


    return (
        <AuthProvider>
            <PreferencesProvider>
                <Router>
                    <Switch>
                        <PrivateRoute
                            exact path="/scenario/:scenarioId"
                            authFlowLayout={UnauthedLayout}
                            component={Scenario}
                        />
                        // default (catchall) route
                        <PrivateRoute
                            authFlowLayout={UnauthedLayout}
                            component={ScenarioList}
                        />
                    </Switch>
                </Router>
            </PreferencesProvider>
        </AuthProvider>
    )
}


export default App;