import React from "react"

import { Link } from "react-router-dom"

import { Container, Row, Col } from "reactstrap"

import MDBNavBar from "../components/MDBNavBar"

const ScenarioList = (props) => {
    const scenarios = [
        {id: 1, name: "PHTC M2CA"},
        {id: 2, name: "Some Other Scenario"}
    ]

    return (
        <>
            <MDBNavBar title="Scenario List" />
            <Container>
                <Row>
                    <Col sm="3"></Col>
                    <Col sm="6">
                        <h2>Your Assigned Scenarios:</h2>
                        <ul>
                            {scenarios.map((scen) => (
                                <li key={scen.id}>
                                    <Link className="h3" to={`/scenario/${scen.id}`}> {scen.name}</Link>
                                </li>
                            ))}
                        </ul>
                    </Col>
                    <Col sm="3"></Col>
                </Row>
            </Container>
        </>
    )
}

export default ScenarioList