import React from "react"

import { Button, ButtonGroup } from "reactstrap"

import { usePrefs } from "../contexts/PreferencesContext"


const LayoutButtons = () => {
    const { isCurrentLayout, setCurrentLayout } = usePrefs()

    return (
        <ButtonGroup>
            <Button
                color={isCurrentLayout("layout1") ? "success" : "primary"}
                onClick={() => setCurrentLayout("layout1")}
            >
                Layout 1
            </Button>
            <Button
                color={isCurrentLayout("layout2") ? "success" : "primary"}
                onClick={() => setCurrentLayout("layout2")}
            >
                Layout 2
            </Button>
        </ButtonGroup>
    )
}

export default LayoutButtons