import React, { useContext } from 'react';

import {
    Button,
    Nav,
    Navbar,
    NavbarBrand,
    NavbarText,
} from "reactstrap";

import { AuthContext } from "@meddbriefer/mdb-auth"

// {/* <Button color="primary" onClick={prefsToggler}>
//     Preferences
// </Button> */}


const MDBNavBar = ({title, leftNav = null, rightNav = null }) => {

    const { isAuthenticated, userName, signOutUser } = useContext(AuthContext)

    return (
        <Navbar color="dark" dark>

            <NavbarBrand href="#">
                MedDBriefer
            </NavbarBrand>

            {!!leftNav &&
                <Nav>
                {leftNav}
                </Nav>
            }

            {!!title &&
                <NavbarText tag="h3">{title}</NavbarText>
            }

            <Nav>
                {isAuthenticated &&
                <>
                    <NavbarText>
                        Welcome, {userName} &nbsp;
                    </NavbarText>
                    <Button
                        color="danger"
                        onClick={signOutUser}
                    >
                        Sign Out
                    </Button>
                </>
                }
                {!!rightNav &&
                    rightNav
                }
            </Nav>

        </Navbar>
    )
}

export default MDBNavBar;
